/* TRANSPORT */

.transport {
	// SLIM
	&.slim {
		> .top, > .right, > .bottom, > .left {
			width: 10px;
			height: 16px;
			padding: 0 0 0 3px;
			margin: 0;

			font-family: @transport-slim-fontfamily;
			line-height: 30px;
			text-align: center;

			border-color: @transport-slim-bordercolor;
			border-width: 0;

			.fontsize(@transport-slim-fontsize);
			.inactive;
			&:after,
			&::after {
				display: inline-block;
				width: 30px;
				height: 30px;

				line-height: 30px;
				text-align: center;
			}
			&:focus {
				outline: 0;
				&:after,
				&::after {
					outline: auto 5px #98c1e9;
				}
			}
			&:hover {
				&:after,
				&::after {
					color: @transport-slim-color-enabled-hover;
				}
			}
		}
		> .top, > .right, > .bottom, > .left {
			&:after,
			&::after {
				color: @transport-slim-color-enabled;
			}
		}
		> .top, > .bottom {
			.rotate(-90);
		}
		> .top {
			&:after,
			&::after {
				content: @transport-slim-arrow-top;
			}
		}
		> .right {
			&:after,
			&::after {
				content: @transport-slim-arrow-right;
			}
		}
		> .left {
			&:after,
			&::after {
				content: @transport-slim-arrow-left;
			}
		}
	}
}

// new one-line transports. could this be the future?
.transport {
	@transport-width: 5px;
	&-top,
	&-right,
	&-bottom,
	&-left {
		position: relative;
		top: 3px;
		left: 4px;

		display: inline-block;

		cursor: pointer;

		border-top: @transport-width solid transparent;
		border-right: @transport-width solid transparent;
		border-bottom: @transport-width solid transparent;
		border-left: @transport-width solid transparent;

		.inactive;
	}
	&-top {
		top: -3px;

		border-bottom: @transport-width solid @colorS4;
	}
	&-right {
		top: 0;
		left: 7px;

		border-left: @transport-width solid @colorS4;
	}
	&-bottom {
		border-top: @transport-width solid @colorS4;
	}
	&-left {
		top: 0;
		left: 2px;

		border-right: @transport-width solid @colorS4;
	}
}
