/* BUTTON GROUP */

.btn-group {
	position: relative;

	display: inline;

	white-space: nowrap;
	> .transport {
		display: inline-block;
		> .top, > .right, > .bottom, > .left {
			position: relative;
			right: -4px;

			border-width: 5px;
		}
		> .top {
			top: -2px;

			margin-top: 2.5px;
			margin-bottom: 2.5px;
		}
		> .right {
			top: 1px;

			margin-left: 2.5px;

			border-right-width: 0;
		}
		> .bottom {
			top: 4px;

			margin-top: 2.5px;
			margin-bottom: 2.5px;
		}
		> .left {
			margin-right: 2.5px;
			margin-left: 0;

			border-left-width: 0;
		}
	}
	// multiple inline buttons
	> .btn, > .btn-mini {
		&:first-child {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}
		+ .btn, + .btn-mini {
			margin-left: -6px;

			border-radius: 0;
		}
		&:last-child {
			border-top-right-radius: 3px;
			border-bottom-right-radius: 3px;
		}
	}
}
