/* BREADCRUMB */

// comment
.breadcrumb {
	padding: 10px 0;
	margin: 0;
	.list-item {
		display: table-cell;

		.fontsize(1.3);
		
		// add the dividers between the breadcrumb items
		&:before,
		&::before {
			padding: 0 10px;

			content: @breadcrumb-content;
		}
		// remove breadcrumb divider for first item
		&:first-child:before,
		&:first-child::before {
			display: none;
		}
		// this removes the white space between the :before and the txt-link
		> .txt-link {
			margin-left: -4px;
			// without this the first item is too far left
			&:first-child,
			&:first-child {
				margin-left: 0;
			}
		}
	}
	// clear after the breadcrumb
	&:after,
	&::after {
		display: table;
		clear: both;

		content: '';
	}
}
